import React, { Component } from 'react';
import { connect } from 'react-redux';
import CardList from '../components/CardList';
import SerchBox from '../components/SerchBox';
import Scroll from '../components/Scroll';
import ErrorBoundry from '../components/ErrorBoundry';
import './App.css';

import { setSerchField } from '../actions'

const mapStateToProps = state => {
	return {
		searchField: state.searchField
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		onSerchChange: (event) => dispatch(setSerchField(event.target.value))
	}
}

class App extends Component {
	constructor() {
		super()
		this.state = {
			robots: []
		}
	}

	componentDidMount() {
		fetch('https://jsonplaceholder.typicode.com/users')
			.then(response => response.json())
			.then(user => this.setState({ robots: user }));
	}

	render() {
		const { robots } = this.state;
		const { searchField, onSerchChange } =this.props;
		const filteredRobots = robots.filter(robot => {
			return robot.name.toLowerCase().includes(searchField.toLowerCase())
		})
		return !robots.length ?
			<h1 className='tc f1'>Loading...</h1> :
			(
				<div className='tc'>
					<h1 className='f1'>RoboFrieds</h1>
					<SerchBox serchchange={onSerchChange} />
					<Scroll>
						<ErrorBoundry>
							<CardList robots={filteredRobots} />
						</ErrorBoundry>
					</Scroll>
				</div>
			);
		}
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
